.product-img {
  width: 100%;
  height: 25rem;
}

.bg-img {
  width: 100%;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

ul.product-img__slides {
  position: relative;
  width: 100%;
  height: 100%;
}
ul.product-img__slides li {
  width: 100%;
  height: 100%;
  position: absolute;
}
ul.product-img__slides li a {
  position: absolute;
  width: 100%;
  height: 100%;
}