@use '../utils/functions' as *;
.product-img{
  width: 100%;
  height: rem(400);
}
.bg-img{
  width: 100%;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

ul.product-img__slides{
  position: relative;
  width: 100%;
  height: 100%;
  li{
    width: 100%;
    height: 100%;
    position: absolute;
    a{
      position: absolute;
      width: 100%;
      height: 100%;
    }
  }
}